import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import Profile from './Profile';
import { getUserProfile, getUserStatus, updateUserStatus, savePhoto } from './../../redux/profile-reducer';
import { withRouter } from 'react-router-dom';
import { compose } from 'redux';
import { withAuthRedirect } from '../../hoc/withAuthRedirect';

const ProfileContainer = (props) => {

  useEffect(() => {
    let userId = props.match.params.userId;
    if (!userId) {
      userId = props.autorizedUserId;
      if(!userId) {
        props.history.push("/login")
      }
    }
    props.getUserProfile(userId)
    props.getUserStatus(userId)
  }, [props.match.params.userId, props.autorizedUserId, ])
 

    return (
      <div >
        <Profile {...props} savePhoto={props.savePhoto} isOwner={!props.match.params.userId} profile={props.profile} status={props.status} updateUserStatus={props.updateUserStatus}/>
      </div>
    )
}

let mapStateToProps = (state) => {
  return {
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    autorizedUserId: state.auth.id,
    isAuth: state.auth.isAuth,
  }
}

export default compose(
  connect(mapStateToProps, { getUserProfile, getUserStatus, updateUserStatus, savePhoto }),
  withRouter,
)(ProfileContainer)
